.App {
  background-color: #EFEFEF;
}

.Content {
  max-width: 100%;
  max-height: 100%;
  padding: 7% 10% 0% 10%;  
}

.Header {
  width: 100%;
    height: 20%;
    display: inline-flex;
}

.Pages {
  justify-content: flex-end;
  display: inline-flex;
  width: 80%;
  align-items: center;
  font-size: 1.3vw;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
  color: #29335C;
}

.Pages > div {
  float: left;
  margin-right: 3%;
}

.Pages > div > a {
  color: #29335C;
  text-decoration: none;
}

.LanguageDiv {
  display: inline-flex;
  align-items: center;
  padding-left: 4%;
}

.Flag {
  width: 30px;
  cursor: pointer;
}

.Body {
  padding-top: 2%;
  text-align: center;
  font-size: 1.5vw;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Body > div {
  margin-top: 5%;
}

.Body > div > div > a {
  color: #29335C;
  text-decoration: none;
  font-weight: bold;
}

.Title {
  font-size: 1.6vw;
  font-weight: bold;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #29335C;
}

.ServiceText {
  text-align: left;
}

table {
  margin-top: 4%;
  border-collapse: collapse;
  width: 100%;
}

th, td {
  text-align: left;
  padding: 8px;
  font-size: 1.3vw;
}

tr:nth-child(even){background-color: #D0D8E8}

th {
  background-color: #4F81BD;
  color: white;
}

@media (min-width: 1100px) {
  .Logo {
    height: 80px;
    width: 160px;
    text-align: left;
    object-fit: cover;
    padding-bottom: 2%;
  }

  .CareServiceDiv {
    display: inline-flex;
    height: 420px;
  }

  .FisioServiceDiv {
    display: inline-flex;
    height: 450px;
  }
  
  .CareServiceDiv > div {
    height: 100%;
  }

  .CareImg {
    width: 50%;
    height: 100%;
    object-fit: cover;
    margin-right: 5%;
    border: 3px solid #29335C;
  }
  
  .FisioImg {
    width: 45%;
    height: 100%;
    object-fit: cover;
    margin-left: 5%;
    border: 3px solid #29335C;
  }

  .Footer {
    margin-top: 8%;
    height: 80px;
    width: 100%;
    background-color: #29335C;
  }
}

@media (max-width: 1100px) {
  .Logo {
    height: 40px;
    width: 80px;
    text-align: left;
    object-fit: cover;
    padding-bottom: 2%;
  }

  .CareServiceDiv {
    display: inline-flex;
    flex-wrap: wrap;
  }

  .FisioServiceDiv {
    display: inline-flex;
    flex-wrap: wrap;
  }

  .CareImg {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border: 3px solid #29335C;
    margin-bottom: 3%;
  }
  
  .FisioImg {
    width: 100%;
    height: 240px;
    object-fit: cover;
    border: 3px solid #29335C;
    margin-top: 3%;
  }

  .Footer {
    margin-top: 8%;
    height: 30px;
    width: 100%;
    background-color: #29335C;
  }
}

